import { Box } from 'rebass';
import Title from '../title/title';
import './tracks.css';

const Tracks = () => (
    <div className='tracks'>
        <Title title="Tracks" width={[0.4, 0.3]} />
        <Box className='track-list'>
            <h2>College/University Survey Data</h2>
            <p>
                The datasets available from this search tool offer a wide variety of data ranging from institutional characteristics of colleges, to their test scores and graduation rates.
            </p>
        </Box>

    </div>
);

export default Tracks;